___CSS_LOADER_EXPORT___.push([module.id, `/*estilos generales*/
body {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: medium;
    margin: 0;
    padding: 0;
    border-radius: 10px;
    height: 100vh; /*altura de la ventana grafica*/
    display: flex; /*para que las dos indicaciones sig se puedan efectuar*/
    justify-content: center;/*centar verticalmente*/
    align-items: center;
};
/*especio para el contenedor principal*/
#app {
    background-color: #fafafa;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(7, 1, 6, 0.1); /*centrado en x centrado en y el color de la sombra y el color*/
};
/*estilos para el formulario*/
form {
    display: flex;
    justify-content: space-between;

};
/*estilos al input*/
input {
    width: 75%;
    padding: 8px;
    margin-right: 10px;
    border: 1px solid #ddd;
    border-radius: 3px;
};
/*estilos del boton*/
button {
    padding: 5px 8px; /*arriba abajo e izq derecha*/
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    
};
/*estilos de la lista*/
ul {
    list-style: none;
    padding: 0;
};
/*estilos a los elementos de la lista*/
li {
    background-color: #f4f4f4;
    padding: 10px;
    border: 1px solid #ddd ;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
};
/*estilos paracuando una tarea esta completada*/
li.complet {
    text-decoration: line-through;
    color: #888;
    
